import { createStore } from "vuex";
import axios from "axios";

const store = createStore({
  state: {
    whether: null,
    apiKey: "af5d40d31417d27b1ab736be54b56462",
  },
  mutations: {
    getWeather(state, payload) {
      state.whether = payload;
    },
  },
  actions: {
    async getWeather({ commit, state }, city) {
      try {
        const res = await axios.get(
          `https://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${state.apiKey}`
        );

        let data = res.data[0];
        let { lat, lon } = data;
        let { ru } = data.local_names;

        const res2 = await axios.get(
          `https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&exclude=minutely,hourly,alerts&appid=${state.apiKey}&units=metric&lang=ru`
        );

        let weatherInfo = res2.data;
        let weatherObject = { ...weatherInfo, name: ru };

        commit("getWeather", weatherObject);
        console.log(state.whether);
      } catch (error) {
        console.log(error);
      }
    },
  },
  getters: {
    getFullWeather: (state) => state.whether,
    getDailyWeather(state) {
      let arr = state.whether.daily;
      arr.pop();
      console.log(arr);
      return arr;
    },
  },
});

export default store;
