<template>
  <div class="week__day">
    <h3 class="week__title">
      {{ i == 0 ? "Сегодня" : i == 1 ? "Завтра" : getWeekDay }}
    </h3>
    <p class="week__date">{{ getDay }} {{ getMonth }}</p>
    <div class="week__img">
      <img
        src="@/assets/images/clear.svg"
        alt=""
        v-if="description == 'ясно'"
      />

      <img
        src="@/assets/images/rain.svg"
        alt=""
        v-else-if="description == 'дождь'"
      />
      <img
        src="@/assets/images/small_rain.svg"
        alt=""
        v-else-if="description == 'небольшой дождь и солнце'"
      />
      <img
        src="@/assets/images/small_rain_sun.svg"
        alt=""
        v-else-if="description == 'небольшой дождь'"
      />
      <img src="@/assets/images/mainly_cloudy.svg" alt="" v-else />
    </div>
    <p class="week__temp" v-if="day.temp.day > 0">
      +{{ Math.round(day.temp.day) }}°
    </p>
    <p class="week__temp" v-else>{{ Math.round(day.temp.day) }}°</p>
    <p class="week__feels" v-if="day.feels_like.day > 0">
      +{{ Math.round(day.feels_like.day) }}°
    </p>
    <p class="week__feels" v-else>{{ Math.round(day.feels_like.day) }}°</p>
    <p class="week__desc">{{ day.weather[0].description }}</p>
  </div>
</template>

<script>
import unix from "@/unix.js";

export default {
  props: {
    day: Object,
    i: Number,
  },
  computed: {
    description() {
      return this.day.weather[0].description;
    },
    getMonth() {
      return unix(this.day.dt, "month");
    },
    getWeekDay() {
      return unix(this.day.dt, "weekday");
    },
    getDay() {
      return unix(this.day.dt, "day");
    },
  },
};
</script>

